<template>
  <div id="app">
    <div class="menu">
      <button @click="newRect">New Rect</button>
      <input type="color" id="favcolor" name="favcolor" @change="changeColor" :value="color">
      <!-- <button @click="changeColor">Apply color</button> -->
    </div>
    <v-stage
      ref="stage"
      :config="stageSize"
      @mousedown="handleStageMouseDown"
      @mousemove="handleStageMouseMove"
      @mouseup="handleStageMouseUp"
      @touchstart="handleStageMouseDown"
    >
      <v-layer ref="layer">
        <v-rect v-for="item in rectangles" :key="item.id" :config="item" />
        <v-transformer ref="transformer" />
      </v-layer>
    </v-stage>
  </div>
</template>

<script>
import shortid from 'shortid';
const width = window.innerWidth;
const height = window.innerHeight;
export default {
  name: "App",
  data() {
    return {
        color:'',
        stageSize: {
            width: width,
            height: height,
        },
        rectangles: [
        ],
        selectedShapeName: "",
        selectedShape: null
    };
  },
  methods: {
    handleTransformEnd(e) {
        const rect = this.rectangles.find(
        (r) => r.name === this.selectedShapeName
        );
        rect.x = e.target.x();
        rect.y = e.target.y();
        rect.rotation = e.target.rotation();
        rect.scaleX = e.target.scaleX();
        rect.scaleY = e.target.scaleY();
    },

    handleStageMouseMove(e) {

    },

    handleStageMouseUp() {
    },

    changeColor(e){
        console.log(0);
        if (this.selectedShape) {
            this.selectedShape.fill = e.target.value;
        }
    },

    newRect() {
        this.rectangles = [
            ...this.rectangles,
            {
                rotation: 0,
                x: Math.random()*150,
                y: Math.random()*150,
                width: 100,
                height: 100,
                scaleX: 1,
                scaleY: 1,
                fill: Konva.Util.getRandomColor(),
                name: shortid.generate(),
                draggable: true,
            }
        ]
    },

    handleStageMouseDown(e) {
        if (e.target === e.target.getStage()) {
            this.selectedShapeName = "";
            this.updateTransformer();
            return;
        }

        const clickedOnTransformer = e.target.getParent().className === "Transformer";
        if (clickedOnTransformer) {
            return;
        }

        const name = e.target.name();
        const rect = this.rectangles.find((r) => r.name === name);
        if (rect) {
            this.selectedShape = rect;
            this.color = this.selectedShape.fill;
            this.selectedShapeName = name;
        } else {
            this.selectedShapeName = "";
            this.selectedShape = null;
        }
        this.updateTransformer();            
    },

    updateTransformer() {
        const transformerNode = this.$refs.transformer.getNode();
        const stage = transformerNode.getStage();
        const { selectedShapeName } = this;
        const selectedNode = stage.findOne("." + selectedShapeName);
        if (selectedNode === transformerNode.node()) {
            return;
        }
        if (selectedNode) {
            transformerNode.nodes([selectedNode]);
        } else {
            transformerNode.nodes([]);
        }
            transformerNode.getLayer().batchDraw();
        },
    },
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.menu {
  background-color: grey;
}
button {
    width: 100px;
    height: 50px;
}
</style>



